<template>
  <div>
    <MyDraftsPolicy 
      v-show="isShowList" 
      ref="list" 
      @read="handleRead"
     >
     </MyDraftsPolicy>
     <PolicyManagePreview 
    v-if='!isShowList' 
    :editId="id" 
    :version='version'
    @upsert-end="handleUpsertEnd" 
    @go-back="goBack"> 
    </PolicyManagePreview>
  </div>
</template>
<script>
 import PolicyManagePreview from  './../homePageManage/PolicyManagePreview.vue' //
 import MyDraftsPolicy from './MyDraftsPolicy.vue'
 // pannelType 1   8  3  9 
export default {
  components: {
    MyDraftsPolicy,
    PolicyManagePreview
  },
  data() {
    return {
      isShowList: true,
      id: undefined,
      mode: 'create',
      version: undefined,
      pannelType: undefined
    };
  },
  methods: {
    handleUpsertEnd () {
      this.isShowList = true
      this.$refs.list.fetchData();//
      this.mode = 'list'
      this.version = undefined
    },
    handleRead(row) {
      this.id = row.policyId
      this.mode = 'read'
      this.version = row.currentVersion
      this.isShowList = false
    },
    /**
     * 
    */
    goBack () {
     this.isShowList = true
     this.mode = 'list'
     this.version = undefined
    }
  }
}
</script>
